import { Table } from 'antd'
import { EyeOutlined } from '@ant-design/icons';
import React, { Component } from 'react'

export default class OldData extends Component {
    columns: any = [];
    constructor(props: any) {
        super(props);
    this.columns = [
        { title: 'SunName',  dataIndex: 'TenantName' },
        { title:'FatherName' , dataIndex: 'BillName' },
        { title: 'ContactNumber', dataIndex: 'Currency' },
        { title: 'Address',  dataIndex: 'InvPeriod',},
        { title:'ViewBill' ,
            dataIndex: 'ViewBill',
            // align: "center",
            render: (text: any, record: any, index: any) =>
            //     <Link to={`${`./ThreePLInvoice`}/${record.TenantID}/${record.EffectiveFrom}/${record.EffectiveTo}`} >
            //         <span >
            //             <Tooltip placement="right" title="View">
                            <EyeOutlined />
            //             </Tooltip>
            //         </span>
            //     </Link>
        },
    ];
    }
  render() {
    return (
      <div>
        <Table
          columns={this.columns}
        //  dataSource={InvoiceGeneration}
        virtual scroll={{ x: 2000, y: 500 }}  />
      </div>
    )
  }
}
