
import { Input } from "antd";
import React, { Component } from "react";
import "../CSS/Login.css";
import { NavLink } from "react-router-dom";

interface IState {
    email: string;
    password: string;
    // islogin:boolean
}

export default class Login extends Component<any, IState> {
    LoginData: any = [];
    child: any;
    constructor(props: any) {
        super(props);
        this.state = {
            email: "",
            password: "",
            // islogin:false
        };

    }
    handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        this.setState({ [name]: value } as Pick<IState, keyof IState>);
    };

   
    componentDidMount(): void {
        this.setState({ email: "", password: "" });
    }
    render() {
        return (
            <>
                <div className="Main_login">
                    <div className="coursel">
                        <div>
                            <div>
                                <h2 className="Gap">!     </h2>
                            </div>
                            <h5>

                                                             
                            </h5>

                        </div>
                    </div>
                    <div className="Login_body">
                        {/* <div className="Image_title"></div>
                        <h1>Login</h1> */}
                        <form>
                            <div className="Login_Form">
                                <div className="divFlex">
                                    <Input
                                        type="email"
                                        name="email"
                                        placeholder="Enter your email"
                                        value={this.state.email}
                                        onChange={this.handleChange}
                                    />
                                    {/* <label>Email ID </label> */}
                                    <span className="required-label"></span>
                                </div>
                                <div className="divFlex">
                                    <Input
                                        type="password"
                                        name="password"
                                        placeholder="Enter your password"
                                        value={this.state.password}
                                        onChange={this.handleChange}
                                    />
                                    {/* <label>Password :</label> */}
                                    <span className="required-label"></span>
                                </div>
                                <div>
                                    {/* <Captcha ref={this.child} /> */}
                                </div>
                                <div className="sign_in_out_buttons">
                                    {(this.state.email === "admin" && this.state.password === '12345') ? (
                                        <button className="Sign_in" ><NavLink to="/Certificate">Login</NavLink></button>
                                    ) : (
                                        ""
                                    )}
                                </div>

                            </div>

                        </form>


                    </div>
                </div>


            </>
        );
    }
}
